import { Injectable } from '@angular/core';
import { JwtHelperService } from '@auth0/angular-jwt';

import { VerdocsStateService } from './state.service';

const helper = new JwtHelperService();
@Injectable()
export class VerdocsAuthService {

  constructor(
    private stateService: VerdocsStateService
  ) {}
  
  public isAuthenticated() {
    const token = this.stateService.getIDToken();
    if (token) {
      const isTokenExpired = helper.isTokenExpired(token);
      return !isTokenExpired;
    }
    return false;
  }
}
