import { Directive, Output, Input, OnInit, EventEmitter, Inject, forwardRef, OnDestroy, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { MatDialog } from '@angular/material';
import { Subscription } from 'rxjs';
import { VerifyEmailDialogComponent } from '../dialogs/verify-email-dialog.component';
import { VerdocsAuthGuardService } from '../services/authguard.service';
import { VerdocsStateService } from '../services/state.service';
import { VerdocsAuthService } from '../services/auth.service';
import { EmailVerificationData } from '../models/email-verification-data.model';

@Directive({
  selector: '[verdocs-guard]'
})
export class VerdocsTokenGuardDirective implements OnInit, OnDestroy {
  private verifyEmailDialogIsOpen = false;
  private emailAddress: string;
  private idTokenSubscription = new Subscription();

  @Output() private resendEmailVerificationEvent = new EventEmitter();
  @Input() private enforceAuthentication;

  constructor(
    private dialog: MatDialog,
    @Inject(forwardRef(() => VerdocsAuthGuardService)) private verdocsAuthGuard: VerdocsAuthGuardService,
    @Inject(forwardRef(() => VerdocsStateService)) private verdocsStateService: VerdocsStateService,
    @Inject(forwardRef(() => VerdocsAuthService)) private verdocsAuthService: VerdocsAuthService,
    @Inject(PLATFORM_ID) private platform
  ) {
  }

  ngOnInit() {
    this.idTokenSubscription = this.verdocsStateService.decodedIdTokenSubject.subscribe(decodedIdToken => {
      this.emailAddress = decodedIdToken.email;
    });
    setInterval(() => {
      if (this.enforceAuthentication) {
        this.verdocsAuthGuard.checkAuthentication();
      }
      if (this.verdocsAuthService.isAuthenticated()) {
        this.checkEmailVerification();
      }
    }, 5000);
  }

  ngOnDestroy() {
    this.idTokenSubscription.unsubscribe();
  }

  private checkEmailVerification() {
    const emailVerified = this.verdocsStateService.getEmailVerification()
    if (!emailVerified && !this.verifyEmailDialogIsOpen && isPlatformBrowser(this.platform)) {
      this.verifyEmailDialogIsOpen = true;
      const email_data: EmailVerificationData = {
        resendEmailVerification: () => this.resendEmailVerification(),
        emailAddress: this.emailAddress
      }
      const verifyEmailDialog = this.dialog.open(VerifyEmailDialogComponent, {
        data: email_data,
        disableClose: true
      });
      verifyEmailDialog.afterClosed().subscribe(() => {
        this.verifyEmailDialogIsOpen = false;
        location.reload();
      });
    }
  }

  resendEmailVerification() {
    this.resendEmailVerificationEvent.emit();
  }
}
