import { Component, Inject, OnInit, forwardRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { VerdocsAuthGuardService } from '../services/authguard.service';
import { VerdocsStateService } from '../services/state.service';
import { EmailVerificationData } from '../models/email-verification-data.model';

@Component({
  selector: 'verdocs-verify-email-dialog.component',
  templateUrl: 'verify-email-dialog.component.html',
  styleUrls: ['verify-email-dialog.component.scss']
})

export class VerifyEmailDialogComponent implements OnInit {
  constructor(
    private dialogRef: MatDialogRef<VerifyEmailDialogComponent>,
    @Inject(forwardRef(() => VerdocsAuthGuardService)) private vTokenAuthGuard: VerdocsAuthGuardService,
    @Inject(forwardRef(() => VerdocsStateService)) private vTokenStateService: VerdocsStateService,
    @Inject(MAT_DIALOG_DATA) public data: EmailVerificationData
  ) { }

  ngOnInit() {
    setInterval(() => {
      if (this.vTokenStateService.getEmailVerification()) {
        this.closeDialog();
      }
    }, 10000);
  }

  signOut() {
    this.vTokenAuthGuard.signOut(window.location.href);
  }

  resendEmailVerification() {
    this.data.resendEmailVerification();
  }

  closeDialog() {
    this.dialogRef.close();
  }
}
